    export default {
        name: 'my-order',
        data () {
            return {
                orderList: {}
            }
        },
        created () {
            this.getOrderList()
        },
        methods: {
          getOrderList () {
              var that = this
              var formdata = new URLSearchParams();
              that.axiosPost('/client/orders', formdata).then((res) => {
                  that.orderList = res.data
              }, (err) => {
                  console.log(err)
              })
          },
          orderDetail(item){

          }

        }
    }



